import React, { useContext, useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import { AuthContext } from "../context/AuthContext";
import "../../App";
import "./Header.css";

function Header() {
  const { isLoggedIn, userInfo, handleLogout } = useContext(AuthContext);
  const [showProfile, setShowProfile] = useState(false);
  const navigate = useNavigate();

  //     
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (!event.target.closest(".profile-modal") && !event.target.closest(".profile-button")) {
        setShowProfile(false);
      }
    };

    document.addEventListener("click", handleClickOutside);
    return () => {
      document.removeEventListener("click", handleClickOutside);
    };
  }, []);

  return (
    <header className="main-header">
      <div className="logo-section" onClick={() => navigate("/")}>
        <img
          src="https://hongfolio.s3.ap-northeast-2.amazonaws.com/logo.png" //     
          alt="HongFolio Logo"
          className="logo-image"
        />
        <h1 className="clickable-title">HongFolio</h1>
      </div>
      <div className="nav-links">
        {!isLoggedIn ? (
          <div>
            <Link to="/login" className="nav-button"></Link>
            <Link to="/signup" className="nav-button"></Link>
          </div>
        ) : (
          <div className="profile-section">
            {/*      */}
            <button
              className="profile-button"
              onClick={(e) => {
                e.stopPropagation(); //      
                setShowProfile(!showProfile);
              }}
            >
              {userInfo?.nickname || "Profile"}
            </button>

            {/*   */}
            {showProfile && (
              <div className="profile-modal">
                <p><strong>:</strong> {userInfo.username}</p>
                <p><strong>:</strong> {userInfo.nickname}</p>
                <p><strong>E-mail:</strong> {userInfo.email}</p>
                <button className="logout-button" onClick={handleLogout}>
                  
                </button>
              </div>
            )}
          </div>
        )}
      </div>
    </header>
  );
}

export default Header;
